.auth-form-container{
    display:flex;
    justify-content: center;    
  /* 垂直置中 */
  align-items: center; 
  padding-top: 10rem

}
.Form{
    display:flex;
    flex-direction: column;
    height: 30rem;
    width: 40rem;
    background-color: #bcdef9;
    border-radius: 3rem 3rem;
    padding: 5rem 4rem;
}
label{
    text-align: left;
    padding:1.5rem 0;

}
input{
    display:flex;
    margin:1rem 0;
    border-radius: 3rem 3rem;
    padding: 1rem ;
    width: 37rem;
    border-color: #c9f0f8;
    background-color: #ebf9fe;

}
button{
border: none;
border-radius: 10rem 10rem;/*原角*/
background-color: #4c829668;
padding: 0.25rem 0;/**/

}
p{
    display: flex;
    justify-content: center;    
    /* 垂直置中 */
    align-items: center; 
}
span{
    
    background:none; 
    text-decoration: underline;
    color:#fff;

}
.Btn{
    margin:3rem 0 0;
    height: 2.75rem;
}